.header {
  width: 100%;
  z-index: 50;
  position: fixed;
  top: 0;
  left: 0;
}

.header:before {
  content: "";
  width: 100%;
  height: 100%;
  z-index: 2;
  background-color: #fefafc;
  position: absolute;
  top: 0;
  left: 0;
