@media (max-width: 376px) {
  .header-container {
    height: 50px;
    padding-right: 18px;
  }

  .header-burger {
    width: 30px;
    height: 20px;
    z-index: 3;
    display: block;
    position: relative;
  }

  .header-burger:before, .header-burger:after {
    content: "";
    height: 2px;
    width: 100%;
    background-color: #21243d;
    transition: all .4s;
    position: absolute;
    left: 0;
  }

  .header-burger:before {
    top: 0;
  }

  .header-burger.active:before {
    top: 9px;
    transform: rotate(45deg);
  }

  .header-burger.active:after {
    bottom: 9px;
    transform: rotate(-45deg);
  }

  .header-burger.active span {
    transform: scale(0);
  }

  .header-burger:after {
    bottom: 0;
  }

  .header-burger span {
    height: 2px;
    width: 100%;
    background-color: #21243d;
    transition: all .4s;
    position: absolute;
    top: 9px;
    left: 0;
  }

  .menu-list {
    width: 100%;
    height: 100%;
    z-index: 1;
    background-color: #edf7fa;
    padding: 70px 0 0 40px;
    display: block;
    position: fixed;
    top: -100%;
    left: 0;
  }

  .menu-list.active {
    transition: all .5s;
    top: 0;
  }

  .nav-link {
    font-size: 50px;
    font-weight: 500;
    line-height: 95px;
  }
}

